<template>
    <div class="mainBox">
        <header>
            <el-col :span="3">
                <span class="diyFont2">T o o l</span><br>
                <span class="diyFont2">s y s t e m</span>
            </el-col>
            <el-col :span="21">
                <div class="headImg">
                    <img src="@/assets/img/loginBG.jpg">
                    <p>
                        <span class="self">个人中心</span>
                        <span class="signout" @click="$router.push({ path: '/' })">退出账号</span>
                    </p>
                </div>
            </el-col>
        </header>
        <el-row class="mainLayer">
            <el-col :span="3" class="menu">
                <el-menu default-active="2" class="el-menu-vertical-demo" background-color="rgba(14, 116, 141, 0.603)"
                    text-color="#fff" active-text-color="#ffd04b">
                    <el-submenu index="1">
                        <template slot="title">
                            <i class="el-icon-menu"></i>
                            <span>工具箱</span>
                        </template>
                        <el-menu-item index="1-3" @click="jump('doorCount')">
                            <i class="el-icon-document"></i>
                            <span>门计算</span>
                        </el-menu-item>
                        </el-menu-item-group>
                    </el-submenu>
                    <el-menu-item index="2" @click="$message.error('暂无权限')">
                        <i class="el-icon-remove"></i>
                        <span slot="title">暂无权限</span>
                    </el-menu-item>
                    <el-menu-item index="3" @click="$message('尽请期待')">
                        <i class="el-icon-loading"></i>
                        <span slot="title">正在开发...</span>
                    </el-menu-item>
                </el-menu>
            </el-col>
            <el-col :span="21" class="mainLayer_main">
                <keep-alive>
                    <router-view v-if="$route.meta.keepAlive"></router-view>
                </keep-alive>
                <router-view v-if="!$route.meta.keepAlive"></router-view>
            </el-col>
        </el-row>
        <div class="live2dBox" v-show="showLive2d">
            <p style="float: left;position: absolute;top:0px;cursor: pointer;font-size: 14px;">
                <span @click="showLive2d=false">赶走小赵</span>
                <span @click="showLive2dchange"
                    style="color: rgb(94, 21, 230);display: block;margin-top: 10px;">小赵换装</span>
            </p>
            <canvas width="200" height="357" id="model"></canvas>
        </div>
        <p v-show="!showLive2d" class="startLive2d" @click="showLive2dShow"> 召 <br> 唤 <br> 小 <br> 赵 </p>
    </div>
</template>
<script>
    import '@/libs/live2d.js' //locationStorage
    export default {
        name: "layout",
        data() {
            return {
                live2d: [
                    'https://cdn.jsdelivr.net/gh/QiShaoXuan/live2DModel@1.0.0/live2d-widget-model-shizuku/assets/shizuku.model.json',
                    'https://cdn.jsdelivr.net/gh/QiShaoXuan/live2DModel@1.0.0/live2d-widget-model-haru/01/assets/haru01.model.json',
                    'https://cdn.jsdelivr.net/gh/QiShaoXuan/live2DModel@1.0.0/live2d-widget-model-haru/02/assets/haru02.model.json',
                    'https://cdn.jsdelivr.net/gh/QiShaoXuan/live2DModel@1.0.0/live2d-widget-model-haruto/assets/haruto.model.json',
                    'https://cdn.jsdelivr.net/gh/QiShaoXuan/live2DModel@1.0.0/live2d-widget-model-hijiki/assets/hijiki.model.json',
                    'https://cdn.jsdelivr.net/gh/QiShaoXuan/live2DModel@1.0.0/live2d-widget-model-izumi/assets/izumi.model.json',
                    'https://cdn.jsdelivr.net/gh/QiShaoXuan/live2DModel@1.0.0/live2d-widget-model-nipsilon/assets/nipsilon.model.json',
                    'https://cdn.jsdelivr.net/gh/QiShaoXuan/live2DModel@1.0.0/live2d-widget-model-unitychan/assets/unitychan.model.json',
                    'https://cdn.jsdelivr.net/gh/QiShaoXuan/live2DModel@1.0.0/live2d-widget-model-wanko/assets/wanko.model.json',
                    'https://cdn.jsdelivr.net/gh/QiShaoXuan/live2DModel@1.0.0/live2d-widget-model-z16/assets/z16.model.json',
                ],
                liveIndex: 8,
                showLive2d: true,
            };
        },
        mounted() {
            if(this.$ls.get('live2dIndex')){
              this.liveIndex = this.$ls.get('live2dIndex')
            }
            loadlive2d("model", this.live2d[this.liveIndex])
        },
        watch: {
        },
        methods: {
            showLive2dShow() {
                this.showLive2d = true,
                    loadlive2d("model", this.live2d[this.liveIndex])
            },
            showLive2dchange() {
                this.liveIndex++
                if (this.live2d.length == this.liveIndex) {
                    this.liveIndex = 0
                }
                this.$ls.set('live2dIndex',this.liveIndex)
                this.showLive2dShow()
            }
        }
    };
</script>


